export default {
	data() {
		return {
			time:false,
			listArr:[],
			ind:null
		}
	},
	onLoad(options) {
		this.getListArr()
	},
	onShow(){
		
	},
	methods: {
		quXiao:function(item,i){
			let that=this;
			uni.showModal({
			        title: '确定要取消吗',
			        content: "订单ID为："+item.id,
			        cancelText:'是',
			        confirmText:'否',
			        success(res){
			          if(res.cancel){
						  let usurl=that.url1+"/dinghai/order/deleteRecruit";
						  	uni.request({
						  		url: usurl,
						  		header:{
						  			"Content-Type" : "application/x-www-form-urlencoded"
						  		},
						  		method:'POST',
						  		data: {
									id:item.id
						  		},
						      success: (res) => {
								  uni.showToast({
								  	title:"已成功取消"
								  })
								  that.listArr.splice(i,1)
						      },
						  	fail : function(err){
						  		uni.showToast({
						  			title:"您的网络打了个盹",
						  			icon:"none"
						  		})
						  	}
						  });
			          }else if(res.confirm){
			             console.log("点击了取消");
			          }
			        }
			   })
			
				
		},
		showList:function(i){
			if(this.ind!=i){
				this.ind=i
			}else{
				this.ind=null
			}
		},
		getListArr:function(){
			let that=this;
				let usurl=that.url1+"/dinghai/order/selectOrder";
				uni.request({
					url: usurl,
					header:{
						"Content-Type" : "application/x-www-form-urlencoded"
					},
					method:'POST',
					data: {
						useruid:that.user.uid
					},
			    success: (res) => {
					if(res.statusCode==200){
						that.listArr=res.data.flag
					}else{
						uni.showToast({
							title:"您的网络打了个盹",
							icon:"none"
						})
					}
			    },
				fail : function(err){
					uni.showToast({
						title:"您的网络打了个盹",
						icon:"none"
					})
				}
			});
		},
		back : function(){
		   uni.navigateBack({
		
		   });
		},
		xiuGai:function(){
			uni.navigateTo({
				url:"./xiugai/xiuGai"
			})
		},			   //  点击整体时显示功能
		showgn : function(){
			// uni.showActionSheet({
									
